<template>
  <div class="container">
    <form class="form-block" @submit.prevent="submit" method="POST">
      <div class="form-group">
        <label>Firstname:</label>
        <input type="text" class="form-control capitalize" v-model="fields.fname" required />
      </div>

      <div class="form-group">
        <label>Lastname:</label>
        <input type="text" class="form-control capitalize" v-model="fields.lname" required />
      </div>

      <div class="form-group">
        <label>Email:</label>
        <input type="email" class="form-control" v-model="fields.email" required />
      </div>

      <div class="form-group">
        <label>Gender:</label>
        <br />
        <select v-model="fields.gender" class="custom-select w-75">
          <option value>Gender</option>
          <option value="Male">Male</option>
          <option value="Female">Female</option>
        </select>
      </div>

      <div class="form-group">
        <label>Prayer Cell</label>
        <br />
        <select v-model="fields.cell" class="custom-select w-75" required>
          <!-- <option :value="defaultCell">{{ defaultCell }}</option> -->
          <option value>Select Cell</option>
          <option v-for="(value, index) in cells" :key="index">{{ value }}</option>
        </select>
      </div>

      <div class="form-group">
        <label>Month and Day of Birth:</label>
        <br />
        <select v-model="fields.day" class="custom-select w-25" required>
          <option value>Select</option>
          <option v-for="(value, index) in days" :key="index">{{ value }}</option>
        </select>

        <select v-model="fields.month" class="ml-3 custom-select w-25" required>
          <option value>Select</option>
          <option v-for="(value, index) in months" :key="index">{{ value }}</option>
        </select>
      </div>

      <!-- <div class="form-group" v-if="fields.gender == 'Male'"> -->
      <div class="form-group">
        <label>Month and Day of Marriage:</label>
        <br />
        <select v-model="fields.day1" class="custom-select w-25">
          <option value>Select</option>
          <option v-for="(value, index) in days" :key="index">{{ value }}</option>
        </select>

        <select v-model="fields.month1" class="ml-3 custom-select w-25">
          <option value>Select</option>
          <option v-for="(value, index) in months" :key="index">{{ value }}</option>
        </select>
      </div>

      <button class="btn btn-primary mr-2">Submit</button>
      <router-link class="btn btn-secondary" to="/admin/home">Cancel</router-link>
    </form>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import util from '../lib/util'

export default {
  name: 'AddBirth',
  props: ['message'],

  data: () => {
    return {
      days: util.days(),
      months: ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sept','Oct','Nov','Dec'],
      cells: util.cells(),
      fields: {
        cell: '',
        gender: '',
        day: '',
        month: '',
        day1: '',
        month1: ''
      }
    }
  },

  methods: {
    submit() {
      const day = ('0' + this.fields.day).slice(-2)
      const day1 = (this.fields.day1) ? ('0' + this.fields.day1).slice(-2) : ''

      const month = util.getMonthFromString(this.fields.month)
      const month1 = (this.fields.month1) ? util.getMonthFromString(this.fields.month1) : ''
      const marriedOn = (day1 && month1) ? day1 + '-' + month1 : ''

      this.$http.post('create', {
        firstname: util.capitalize(this.fields.fname),
        lastname: util.capitalize(this.fields.lname),
        email: this.fields.email,
        gender: this.fields.gender,
        cell: this.fields.cell,
        bornAt: day + '-' + month,
        marriedOn
      })
      .then(response => {
        this.fields = {}
        this.$props.message(response.data.message)
      })
      .catch(error => {
        this.$props.message(error)
      })
    }
  },

  created() {
    // console.log(this.defaultCell)
    console.log(this.currentUser.cell)
    this.fields.cell = this.currentUser.cell ? this.currentUser.cell : ''
    // this.$set(this, 'fields.cell', this.currentUser.cell)
  },

  computed: {
    ...mapGetters({ currentUser: 'currentUser' }),
    // defaultCell() {
    //   this.fields.cell = this.currentUser.cell
    // }
  }
}
</script>

