<template>
  <div class="container">
    <form @submit.prevent="UpdateForm" method="POST">
      <div class="form-group">
        <label>Firstname:</label>
        <input type="text" class="form-control" v-model="fields.fname" />
      </div>

      <div class="form-group">
        <label>Lastname:</label>
        <input type="text" class="form-control" v-model="fields.lname" />
      </div>

      <div class="form-group">
        <label>Email:</label>
        <input type="email" class="form-control" v-model="fields.email" />
      </div>

      <div class="form-group">
        <label>Gender:</label>
        <br />
        <select v-model="fields.gender" class="custom-select my-1 mr-sm-2">
          <option value="Male">Male</option>
          <option value="Female">Female</option>
        </select>
      </div>

      <div class="form-group">
        <label>Prayer Cell</label>
        <br />
        <select v-model="fields.cell" class="custom-select w-75" required>
          <option v-for="(value, index) in cells" :key="index">{{ value }}</option>
        </select>
      </div>

      <div class="form-group">
        <label for="month and day">Month and Day of Birth:</label>
        <br />
        <select v-model="fields.day" class="custom-select w-25">
          <option v-for="(value, index) in days" :key="index">{{ value }}</option>
        </select>

        <select v-model="fields.month" class="custom-select w-25 ml-3">
          <option v-for="(value, index) in months" :key="index">{{ value }}</option>
        </select>
      </div>

      <div class="form-group">
        <label>Month and Day of Marriage:</label>
        <br />
        <select v-model="fields.day1" class="custom-select w-25">
          <option value>Select</option>
          <option v-for="(value, index) in days" :key="index">{{ value }}</option>
        </select>

        <select v-model="fields.month1" class="custom-select w-25 ml-3">
          <option value>Select</option>
          <option v-for="(value, index) in months" :key="index">{{ value }}</option>
        </select>
      </div>

      <button class="btn btn-primary mr-2">Update</button>
      <router-link class="btn btn-secondary" to="/admin/home">Cancel</router-link>
    </form>
  </div>
</template>

<script>
import util from '../lib/util'

export default {
  name: 'UpdateBirth',
  props: ['message'],

  data() {
    return {
      id: this.$route.params.id,
      cells: util.cells(),
      fields: {},
      days: util.days(),
      months: ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sept','Oct','Nov','Dec'],
      posts: []
    }
  },

  created() {
    this.$http('read/' + this.id)
    .then(res => {
      const data = res.data.doc[0]
      const info = {
        fname : data.firstname,
        lname : data.lastname,
        email : data.email,
        gender : data.gender,
        cell : data.cell,
        day : data.bornAt.slice(0, 2),
        day1 : data.marriedOn.slice(0, 2),
        month : util.monthName(data.bornAt.slice(3)),
        month1 : util.monthName(data.marriedOn.slice(3))
      }
      this.$set(this, 'fields', info)
    })
    .catch(error => {
      this.$props.message(error)
    })
  },

  methods: {
    UpdateForm() {
      const day = ('0' + this.fields.day).slice(-2)
      const day1 = ('0' + this.fields.day1).slice(-2)
      const month = util.getMonthFromString(this.fields.month)
      const month1 = util.getMonthFromString(this.fields.month1)

      this.$http.put('update/' + this.id, {
        firstname: util.capitalize(this.fields.fname),
        lastname: util.capitalize(this.fields.lname),
        email: this.fields.email,
        gender: this.fields.gender,
        cell: this.fields.cell,
        bornAt: day + '-' + month,
        marriedOn: day1 + '-' + month1
      })
      .then(response => {
        this.$props.message(response.data)
        this.fields = ''
      })
      .catch(error => {
        this.$props.message(error)
      })
    }
  }
}
</script>

<style scoped>
.container {
  margin-top: 2rem;
  width: 50%;
}

.w-25 {
  width: 5.4rem !important;
}

ul li {
  list-style-type: none;
  margin: 30px 30px 30px 0;
  padding: 30px 30px 30px 0;
}
</style>
