import Vue from 'vue'
import Vuex from 'vuex'
import User from './user'

const LOGIN = 'LOGIN'
const LOGOUT = 'LOGOUT'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: User.from(localStorage.getItem('token'))
  },

  mutations: {
    [LOGIN] (state) {
      state.user = User.from(localStorage.getItem('token'))
    },
    [LOGOUT] (state) {
      state.user = null
    }
  },

  actions: {
    login ({ commit }) {
      commit(LOGIN)
    },

    logout ({ commit }) {
      commit(LOGOUT)
    }
  },

  getters: {
    currentUser (state) {
      // console.log(state.user.isAdmin)
      return state.user
    }
  }

})
