<template>
  <div class="pagination">
    <button class="btn" @click="prev" :disabled="!prevPage">Prev</button>
    <span>page {{currentPage}} of {{totalPage}}</span>
    <button class="btn" @click="next" :disabled="!nextPage">Next</button>
  </div>
</template>

<script>
export default {

  props: {
    pager : { type: Object }
  },

  methods: {
    prev() {
      this.$emit('pagechanged', this.$props.pager.currentPage - 1);
    },
    next() {
      this.$emit('pagechanged', this.$props.pager.currentPage + 1);
    }
  },

  computed: {
    prevPage(){
      return this.$props.pager.prevPage
    },
    nextPage(){
      return this.$props.pager.nextPage
    },
    currentPage(){
      return this.$props.pager.currentPage
    },
    totalPage(){
      return this.$props.pager.totalPage
    }
  }

}
</script>

<style scoped>
.pagination {
  align-items: center;
  width: 20%;
  margin-left: 30%;
}

.btn {
  margin: 0.2rem 0.5rem 0 0.5rem;
  border: 1px solid #bbb !important;
}

button:focus {
  box-shadow: none !important;
}

button:disabled {
  cursor: not-allowed;
}

.active {
  background-color: #4aae9b;
  color: #ffffff;
}
</style>
