<template>
  <transition name="modal-fade">
    <div class="overlay">
      <div
        class="modal-content"
        role="dialog"
        aria-labelledby="modalTitle"
        aria-describedby="modalDescription"
      >
        <slot name="body">
          <div class="container-fluid">
            <p>
              Are you sure you want to delete
              <strong>{{ this.$props.user.name }}</strong>?
            </p>
            <button
              type="button"
              class="btn btn-primary mr-4"
              @click="close"
              aria-label="Close modal"
            >Cancel</button>
            <button
              type="button"
              class="btn btn-secondary"
              @click="confirm"
              aria-label="Close modal"
            >Delete</button>
          </div>
        </slot>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    user : { type: Object }
  },

  methods: {
    close() {
      this.$emit("close")
    },

    confirm() {
      this.$emit("confirm", this.$props.user)
    }
  }
};
</script>

