<template>
  <section class="text-center main-container">
    <form class="form-signin" @submit.prevent="login">
      <img class="image-wrap mb-4" src="../assets/rcnlogo.png" alt width="100" height="100" />
      <!-- <img class="loading" src="../assets/loading.gif" alt="Loading Icon"> -->
      <h1 class="h3 mb-3 font-weight-normal">Please Login</h1>
      <p class="alert alert-danger" v-if="error">{{ error }}</p>

      <label class="sr-only">Email address</label>
      <input type="email" v-model="user.email" class="form-control" required />
      <br />

      <label class="sr-only">Password</label>
      <input type="password" v-model="user.password" class="form-control" required />

      <!-- <div class="checkbox mb-3 text-left">
        <label>
          <input type="checkbox" value="remember-me"> Remember me
        </label>
      </div>-->

      <button class="mt-4 btn btn-lg btn-primary btn-block" type="submit">Login</button>
      <p class="mt-5 mb-3 text-muted">&copy; 2019 - {{ year }}</p>
    </form>
  </section>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data(){
    return {
      year: new Date().getFullYear(),
      user: {},
      error: false
    }
  },

  computed: {
    ...mapGetters({ currentUser: 'currentUser' })
  },

  created () {
    this.checkCurrentLogin()
  },

  updated () {
    this.checkCurrentLogin()
  },

  methods: {
    checkCurrentLogin () {
      if (this.currentUser) {
        this.$router.push(this.$route.query.from || 'admin/home')
      }
    },
    login() {
      this.$http.post('login', { ...this.user })
      .then(res => this.loginSuccessful(res))
      .catch(() => this.loginFailed())
    },

    loginSuccessful (res) {
      if (!res.data.token) {
        this.loginFailed()
        return
      }
      console.log(JSON.parse(atob(res.data.token.split('.')[1])))
      localStorage.token = res.data.token
      this.$store.dispatch('login')

      this.error = false
      this.$router.replace(this.$route.query.from || 'admin/home')
    },

    loginFailed () {
      this.error = 'Login failed!'
      this.$store.dispatch('logout')
      delete localStorage.token
    }
  }
}
</script>

