import Vue from 'vue'
import Router from 'vue-router'
// import store from '../store'
import Home from '../components/Home'
import Main from '../components/Main'
import Login from '../components/Login'
import Logout from '../components/Logout'
import AddBirth from '../components/AddBirth'
// import NotFound from '../components/NotFound'
import SingleBirth from '../components/SingleBirth'
import UpdateBirth from '../components/UpdateBirth'

Vue.use(Router)

const router = new Router({
  mode: 'history',

  routes: [{
    path: '/admin',
    name: 'main',
    component: Main,
    meta: { requiresAuth: true },
    children: [{
      path: 'home',
      component: Home
    },
    {
      path: 'add',
      component: AddBirth
    },
    {
      path: 'read/:id',
      component: SingleBirth
    },
    {
      path: 'update/:id',
      component: UpdateBirth
    }]
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/logout',
    name: 'Logout',
    component: Logout
  },
  {
    path: '/',
    component: Login
  }]
})

export default router
