<template>
  <div>
    <router-link to="/admin/home">
      <img class="back" src="../assets/arrow_left.svg" alt="Home" />
    </router-link>
    <div class="card">
      <img src="../assets/default_user.png" alt="User Image" />
      <ul v-for="(post, index) in posts" :key="index">
        <li>
          <h1>{{ post.firstname + ' ' + post.lastname }}</h1>
        </li>
        <li>
          <img src="../assets/mail.svg" />
          {{ post.email }}
        </li>
        <li>
          <img src="../assets/pray2.svg" />
          {{ post.cell }}
        </li>
        <li>
          <img src="../assets/birthday.svg" />
          {{ post.bornAt }}
        </li>
        <li>
          <img src="../assets/marriage.svg" />
          {{ post.marriedOn }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import util from '../lib/util'

export default {
  name: 'SingleBirth',

  data() {
    return {
      id: this.$route.params.id,
      posts: [],
      errors: []
    }
  },

  created() {
    this.$http.get('read/' + this.id)
    .then(res => {
      const data = res.data.doc
      data.forEach(d => {
        let day = d.bornAt.slice(0, 3)
        let month = util.monthName(d.bornAt.slice(-2))
        d.bornAt = day + month
      })
      this.$set(this, 'posts', data)
    })
    .catch(e => {
      this.errors.push(e)
    })
  }
}
</script>

