
export default class User {
  static from(token) {
    try {
      const obj = JSON.parse(atob(token.split('.')[1]))
      return new User(obj)
    } catch (e) {
      return null
    }
  }

  constructor({ _id, fname, cell }) {
    this.id = _id
    this.fname = fname
    this.cell = cell
  }

  get isAdmin() {
    return this.fname
  }
}
