<template>
  <div class="container">
    <Modal :user="user" v-show="isModalVisible" @confirm="confirmDelete" @close="closeModal" />
    <div class="search-control d-flex flex-row">
      <router-link to="/admin/add">
        <img src="../assets/add.svg" class="add" alt="Add" />
      </router-link>
      <input v-model="search" class="search form-control" placeholder="Firstname or Lastname" />
    </div>
    <table class="table table-hover">
      <thead>
        <tr>
          <th>Firstname</th>
          <th>Lastname</th>
          <th>DOB</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in sortedUsers" :key="user._id">
          <td>{{ user.firstname }}</td>
          <td>{{ user.lastname }}</td>
          <td>{{ user.bornAt }}</td>
          <td>
            <div class="dropdown">
              <button
                class="btn btn-default shadow-none dropdown-toggle"
                type="button"
                data-toggle="dropdown"
              >
                <span class="caret"></span>
              </button>
              <ul class="dropdown-menu">
                <li>
                  <router-link class="btn btn-light" :to="'update/'+ user._id">
                    <img src="../assets/update.svg" alt="update" />
                  </router-link>
                </li>
                <li>
                  <button class="btn btn-light" @click="showModal(user)">
                    <img src="../assets/delete.svg" alt="delete" />
                  </button>
                </li>
                <li>
                  <router-link class="btn btn-light" :to="'read/'+ user._id">
                    <img src="../assets/view.svg" alt="view" />
                  </router-link>
                </li>
              </ul>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination :pager="pager" @pagechanged="onPageChange" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import util from "../lib/util"
import Modal from "../components/Modal.vue"
import Pagination from "../components/Pagination.vue"

export default {
  components: { Modal, Pagination },
  props: ['message'],

  data() {
    return {
      users: [],
      errors: [],
      search: '',
      user: {},
      pager: {},
      isModalVisible: false
    }
  },

  // Fetches posts when the component is created.
  created() {
    this.getUsers(1)
  },

  methods: {

    getUsers(page) {
      let cell = this.currentUser.cell
      cell = (cell) ? cell : ''
      this.$http
        .get("read?limit=7&page="+page+"&cell="+cell)
        .then(res => {
          const data = res.data.doc
          this.makePager(res.data)
          data.forEach(d => {
            let day = d.bornAt.slice(0, 3)
            let month = util.monthName(d.bornAt.slice(-2))
            d.bornAt = day + month
            // this.users.push(d)
          })
          this.$set(this, 'users', data)
        })
        .catch(error => {
          this.$props.message(error)
        })
    },

    makePager: function(data){
      let pager = {
          currentPage: data.currentPage,
          totalPage: data.totalPage,
          nextPage: data.nextPage,
          prevPage: data.prevPage
      }
      this.$set(this, 'pager', pager)
    },

    deleteUser(id) {
      this.$http
        .delete("remove/" + id)
        .then(response => {
          this.users= this.users.filter(user => user._id !== id)
          this.$props.message(response.data)
        })
        .catch(error => {
          this.$props.message(error)
        })
    },

    confirmDelete(user) {
      this.deleteUser(user.id)
      this.closeModal()
      console.log(user.name)
    },

    showModal(user) {
      this.user = {
        id: user._id,
        name: user.firstname + ' ' + user.lastname
      }
      this.isModalVisible = true
    },

    closeModal() {
      this.isModalVisible = false
    },

    onPageChange(page) {
      this.getUsers(page)
    }
  },

  computed: {
    ...mapGetters({ currentUser: 'currentUser' }),

    sortedUsers() {
      return this.users.filter(user => {
        const firstname = user.firstname.match(util.capitalize(this.search))
        const lastname = user.lastname.match(util.capitalize(this.search))
        return firstname ? firstname : lastname
      })
    }
  }
}
</script>

